:root {
  --White: #fff;
  --Light-gray: #d6e2f0;
  --Grayish-blue: #7b879d;
  --Dark-blue: #1f3251;
  --border-radious: 15px;
}

*, :before, :after {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Outfit;
  font-size: 62.5%;
}

body {
  background-color: var(--Light-gray);
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 15px;
  display: flex;
}

.container {
  border-radius: var(--border-radious);
  text-align: center;
  background-color: #fff;
  grid-template-rows: 25rem 10rem;
  place-items: center;
  max-width: 28rem;
  display: grid;
}

.qr-image {
  border-radius: var(--border-radious);
  width: 92%;
  margin-top: 3.5rem;
}

.heading {
  color: var(--Dark-blue);
  margin-top: 2.5rem;
  padding: .5rem 3rem;
}

.description {
  color: var(--Grayish-blue);
  padding: .4rem 1rem 3rem;
}

